import { createRouter, createWebHashHistory } from 'vue-router'
import Login from '@/views/Login.vue'
const router = createRouter({
  history: createWebHashHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      component: () => import('@/views/LayoutPage/LayoutPage.vue'),
      redirect: '/home/partyEx',
      children: [
        {
          path: '/home/partyEx',
          name: 'PartyEx',
          component: () => import('@/views/LayoutPage/PartyEx.vue'),
          children: [
            {
              path: '/home/partyEx/22Y7M',
              name: '22Y7M',
              component: () => import('@/views/LayoutPage/PartyEx/22Y7M.vue')
            }
          ]
        },
        {
          path: '/home/scribble',
          name: 'Scribble',
          component: () => import('@/views/LayoutPage/Scribble.vue')
        },
        {
          path: '/home/tool',
          name: 'AssistTool',
          component: () => import('@/views/LayoutPage/AssistTool.vue'),
          children: [
            {
              path: '/home/tool/translate',
              name: 'translate',
              component: () => import('@/views/LayoutPage/AssitTool/Translate.vue')
            },
            {
              path: '/home/tool/diceRoll',
              name: 'diceRoll',
              component: () => import('@/views/LayoutPage/AssitTool/DiceRoll.vue')
            }
          ]
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.name !== 'Login') {
    const token = localStorage.getItem('token')
    if (token) {
      next()
    } else {
      next({ name: 'Login' })
    }
  } else {
    next()
  }
})

export default router
