<!-- eslint-disable vue/multi-word-component-names -->
<script setup>
import { onMounted, ref } from 'vue'
import LoginForm from '../components/Login/LoginForm.vue'
const bgUrl = ref(null)
const bgRadom = Math.floor(Math.random() * 10 + 1)

const changeBg = () => {
  bgUrl.value = `./img/background/${bgRadom}.jpg`
}
onMounted(() => {
  changeBg()
})
</script>

<template>
  <div class="wrapper">
    <img :src="bgUrl" />
    <LoginForm></LoginForm>
  </div>
</template>

<style scoped>
.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.25s;
  z-index: -1;
}

.wrapper img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  backface-visibility: hidden;
}
</style>
