<script setup>
import { ref, computed } from 'vue'
import { useRouter } from 'vue-router'
const router = useRouter()
const travller = ref('')
const password = ref('')
const isMe = computed(() => {
  return travller.value.match(/^xyb$/)
})
const isTraveller = computed(() => {
  return travller.value.toLowerCase().match(/^zxq$|^syd$|^pmd$|^fy$/)
})

const loginBtn = () => {
  if (isTraveller.value) {
    localStorage.setItem('token', 'traveller')
    localStorage.setItem('name', travller.value.toUpperCase())
    router.push({ name: 'PartyEx' })
  } else if (isMe.value && password.value.match('112358')) {
    localStorage.setItem('token', 'admin')
    localStorage.setItem('name', travller.value.toUpperCase())
    router.push({ name: 'PartyEx' })
  } else {
    window.alert('无法登录')
  }
  travller.value = ''
  password.value = ''
}
</script>

<template>
  <div class="login">
    <h1>xyb.for.record</h1>
    <div class="input-box">
      <input v-model="travller" type="text" placeholder="Traveller" />
    </div>
    <div class="input-box" v-show="isMe">
      <input v-model="password" type="password" placeholder="Password" />
    </div>
    <button class="btn" @click="loginBtn">Login</button>
    <div class="info">
      <p>输入姓名缩写，若无法登录可能有以下原因：</p>
      <p>1、代码没写好；2、无查看权限</p>
    </div>
  </div>
</template>

