.login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 600px;
  backdrop-filter: blur(7px) brightness(90%);
  background-color: transparent;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  padding: 30px 40px;
}

h1 {
  font-size: 36px;
  text-align: center;
  font-family: 'Pacifico-Regular';
  color: #fff;
}

.input-box {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100px;
  margin: 30px 0;
}

.input-box input {
  width: 100%;
  height: 100%;
  background-color: transparent;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  font-size: 20px;
  color: #fff;
  padding: 20px 45px;
}

.input-box input::placeholder {
  color: #fff;
}

.btn {
  width: 100%;
  height: 45px;
  background: #fff;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
  margin: 0 auto;
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 600;
}

.info {
  font-size: 14.5;
  text-align: center;
  margin: 20px 0 15px;
}

.info p {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

@media screen and (max-width: 400px) {
  .login {
    width: 50px;
    height: 50px;
  }
}
</style>
